* {
  box-sizing: border-box;
}

body {
  background-color: #eff3f7;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__text {
  font-size: 2rem;
  font-weight: 600;
}

.movies {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 50px;
  gap: 20px;
  grid-gap: 20px;
}
